import {
	UniPopup
} from 'components/uni/uni-popup/uni-popup'
import {
	GetUserFeePackageDetail,
	GetUserFeePackageBuyDetail,
	InitialPayUserFeePackage,
	PayUserFeePackage
} from 'api/user'
export default {
	components: {
		UniPopup
	},
	data() {
		return {
			type: 0,
			detailData: {},
			id: 0,
			packid: 0,
			state: {},
			money: 0,
			paymentList: [],
			activePay: -1,
			payId: 0,
			initBuyData: {},
			show: false,
			userInfo: {},
			list: [],
			dialog: false,
			baseUrl:this.$store.state.SRC+"/config/app"
		}
	},
	watch: {
		money(money) {
			if (money - this.initBuyData.price >= 0) {
				this.money = this.initBuyData.price;
				this.activePay = -1;
				this.payId = 0;
			} else {
				this.activePay = 0;
				this.payId = this.paymentList[0].payid;
			}
			if (money - this.userInfo.Money > 0) {
				this.money = this.userInfo.money;
			}
			if (this.initBuyData.price > this.userInfo.Money) {
				if (this.activePay == -1) {
					this.activePay = 0;
					this.money = 0;
					this.payId = this.paymentList[0].payid;
				}
			}
		}
	},
	methods: {
		/**打开会话 */
		openDialog(list) {
			this.dialog = true;
			if (list) {
				this.list = list;
			} else {
				this.list = [];
			}
		},
		/**获取我的套餐包详情 */
		getMyData() {

			this.detailData = {};
			GetUserFeePackageDetail({
				id: this.id,
				packid: this.packid
			}).then(res => {
				if (res.result == true) {
					this.detailData = res.data;
				}
			})
		},
		/**获取全部套餐包详情 */
		getAllData() {

			this.detailData = {};
			GetUserFeePackageBuyDetail({
				id: this.id,
				packid: this.packid
			}).then(res => {
				if (res.result == true) {
					this.detailData = res.data;
				}
			})

		},
		buyCard(item) {
			if (this.activePay == -1) {
				PayUserFeePackage({
					packid: item.packid,
					payid: this.payId,
					userpaymoney: this.money,
				}).then(res => {
					if (res.result) {

						this.$store.dispatch("getUserInfo").then(_ => {
							this.$store.ready(state => {
								this.userInfo = state.userInfo;
								this.show = false;
								uni.showToast({
									title: '购买成功',
								})
							});
						})
					} else {
						uni.showToast({
							title: res.msg,
							icon: 'none'
						})
					}
				});
			} else {
				PayUserFeePackage({
					packid: item.packid,
					payid: this.payId,
					userpaymoney: this.money,
				}).then(res => {
					if (res.result) {
						uni.navigateTo({
							url: '/pages/payment/payment?orderid=' + res.orderid + '&ordertype=' + res.ordertype
						})
					} else {
						uni.showToast({
							title: res.msg,
							icon: 'none'
						})
					}
				});
			}
		},
		initPay(item) {

			InitialPayUserFeePackage({
				packid: item.packid,
			}).then(res => {
				if (res.result) {
					this.initBuyData = res.data;
					this.money = this.initBuyData.price;
					this.paymentList = res.data.payment_list;
					this.show = true
				}
			});
		},
		pay() {
			this.show = true
		},
		close(e) {
			this.show = e.show

		}
	},
	onShow() {
		if (this.type == 0) {
			this.getMyData();
		} else {
			this.getAllData();
		}
	},
	onLoad(query) {

		this.type = query.type
		this.id = query.id
		this.packid = query.packid
		this.$store.ready(state => {
			this.state = state
			this.userInfo = state.userInfo
		})

	}
}
